import {SliderContainer, MLeftMenu, MRightMenu} from './EDashboard';
import styled from 'styled-components';
import EmployeeMenu from './EmployeeMenu';

const EAllContainer = styled.div`
    width: 81vw;
    height: 85vh;
    /* background-color: red; */
    padding: 2vh 2vw;

    h2 {
        padding: 1vh 1vw;
        box-shadow: 1px 1px whitesmoke;
        border-bottom: 1px solid #E6E6E6;
    }
`;

const AllCTable = styled.table`
    width: 81vw;
    height: 15vh;
    border-collapse: collapse;

    th {
        border: 1px solid #dddddd;
        text-align: left;
        padding: 8px;
        position: sticky;
        top: 0vh;
        background-color: white;
    }
    td {
        border: 1px solid #dddddd;
        text-align: left;
        padding: 8px;
        border-collapse: collapse;
    }
    tr:nth-child(even) {
        background-color: #f4f8fc;
    }
    tr:nth-child(odd) {
        background-color: white;
        
    }
`

function AccountRequest() {
  return (
    <>
      <SliderContainer>
        <MLeftMenu>
            <EmployeeMenu/>
        </MLeftMenu>
        <MRightMenu>
            <EAllContainer>
                <h2>Account Request</h2>
                <hr/>
                <AllCTable>
                    <tr>
                        <th>First Name</th>
                        <th>Last Name</th>
                        <th>Gender</th>
                        <th>Email</th>
                        <th>Phone Number</th>
                        <th>Dob</th>
                        <th>Aadhaar</th>
                        <th>Pan</th>
                        <th>Profile</th>
                        <th>Account Status</th>
                    </tr>
                    <tr>
                        <td>John</td>
                        <td>Doe</td>
                        <td>male</td>
                        <td>
                            <a href="mailto:nandakumar.k0531@gmail.com">nandakumar.k0531@gmail</a>
                        </td>
                        <td>9876543210</td>
                        <td>1999-12-31</td>
                        <td>123456789012</td>
                        <td>1234567890</td>
                        <td>
                            <a href="https://www.w3schools.com">
                                <button>View</button>
                            </a>
                        </td>
                        <td style={{alignItems:'center'}}>
                            <button>Approve</button>
                            <button style={{marginLeft:'10px'}}>Reject</button>
                        </td>
                    </tr>
                </AllCTable>
            </EAllContainer>
        </MRightMenu>
      </SliderContainer>
    </>
  );
}

export default AccountRequest;